@import "video.js/dist/video-js.css";

#root {
  position: fixed;
  top: 0;
  bottom: 0;
  height: 100%;
  max-width: 100vw;
  width: 100vw;
  margin: auto;
  background-color: var(--background);
}

.root-container {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: hidden;
}

.bottom-nav-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 21px 0;
  background-color: var(--background);
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.bottom-nav-btn {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 31px;
  text-decoration: none;
}

a.bottom-nav-btn {
  color: white;
}

a.bottom-nav-btn.active-nav-btn {
  color: var(--main-blue);
}

.page-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  overflow: auto;
  height: 100%;
}

.ptr__children {
  overflow: hidden auto !important;
  scrollbar-color: #888 var(--background);
  scrollbar-width: thin;
}

.recent-activity-entries {
  background-color: var(--background);
}

.ptr__children::-webkit-scrollbar {
  width: 10px;
}

.ptr__children::-webkit-scrollbar-track {
  background: var(--background);
}

.ptr__children::-webkit-scrollbar-thumb {
  background: #888;
}

.ptr__children::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.inputs-group {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 25px;
  width: 100%;
}

.unit-dropdown {
  font-size: 24px;
  border: 0;
  background-color: transparent;
  font-family: Poppins;
  font-weight: 400;
  height: 55px;
  padding-left: 26px;
  color: white;
}

.unit-dropdown option {
  background-color: var(--black);
}

.form-error {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 13px;
  border-radius: 0px;
  color: white;
  font-weight: bold;
  margin-bottom: 15px;
  background-color: var(--failure-red);
  font-size: 12px;
}

.modal-paragraph {
  text-align: center;
  color: var(--text-white);
}

.inline-link {
  text-align: center;
  font-weight: 600;
  text-decoration: underline;
  margin-top: 20px;
}

.slide-pay-container {
  width: 100%;
  margin-top: 50px;
  padding: 30px 50px;
}

.slide-pay-container.slide-disabled {
  opacity: 0.6;
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
  -webkit-filter: grayscale(0.4);
          filter: grayscale(0.4);
  pointer-events: none;
}

.floaty-container {
  position: absolute;
  z-index: 99999;
  width: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}
.fixed-container {
  position: fixed;
  z-index: 99999;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.global-dialog {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: var(--background);
  padding-top: 5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 2rem;
  border: 2px solid var(--main-blue);
  border-radius: 1rem;
}

@media (display-mode: standalone) {
  #root {
    height: 100% !important;
  }

  .root-container {
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100% !important;
  }

  .page-container {
    -webkit-flex: 1 1;
            flex: 1 1;
  }
}

.cursor-pointer {
  cursor: pointer;
}

.red-text {
  color: var(--failure-red);
}

.red-border {
  border-color: var(--failure-red) !important;
}

.text-align-center {
  text-align: center;
}
